.calendar {
  display: flex;
  flex-wrap: wrap;
  margin: 50px auto;
  width: $box_size * $days_in_week + 7px;

  div {
    @include box-style;
  }

  div:hover {
    @include box_hover_style;
  }
}

@for $i from 28 through 31 {
  .calendar-#{$i} {

    @for $j from 1 through $i {
      & :nth-child(#{$j}) {
        display: block;
      }

      & :nth-child(#{$j})::after {
        content: "#{$j}";
      }
    }
  }
}

@each $num, $day in $start_days {
  .calendar-#{$day} &:first-child {
    margin-left: ($box_size + $gap_size ) * $num;
  }
}